/*
//------------------------------------------------------------------------------
// Login
//------------------------------------------------------------------------------
*/
form#user-login > div {
  background: cornsilk;
  display: flex;
  flex-wrap: wrap;
  background-color: #E6E7E8;
  box-shadow: none;
}

@media only screen and (min-width: 992px) {
  form#user-login > div {
    box-shadow: 15px 15px #F1F1F2;
  }
}

form#user-login .form-type-textfield label {
  text-align: center;
  font-family: "Lato Regular";
  font-size: 3rem;
  line-height: 21.89px;
  color: #000000;
  margin: 0px;
  font-weight: normal;
  margin-top: 25px;
  margin-bottom: 35px;
}

form#user-login .form-type-textfield label::after {
  content: "";
  display: block;
  width: 86%;
  border-top: 2px solid #808184;
  margin: 14px auto 0px;
}

@media only screen and (min-width: 992px) {
  form#user-login .form-type-textfield label {
    font-size: 4rem;
    margin-top: 37px;
    margin-bottom: 54px;
  }
}

form#user-login .form-type-textfield,
form#user-login .form-type-password {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex: 1 1 100%;
}

form#user-login .form-type-textfield input#edit-name,
form#user-login .form-type-password input#edit-pass {
  width: 90%;
  margin: 0 auto;
  background: #ffffff;
  border-radius: 0px;
  font-family: "Lato Light";
  font-size: 1.4rem;
  transition: none;
  box-shadow: none;
}

@media only screen and (min-width: 992px) {
  form#user-login .form-type-textfield input#edit-name,
  form#user-login .form-type-password input#edit-pass {
    width: 70%;
    height: 46px;
    font-size: 20px;
  }
}

form#user-login .form-type-textfield input:-webkit-autofill,
form#user-login .form-type-password input:-webkit-autofill {
  background-color: #ffffff !important;
}

form#user-login div#edit-info {
  display: none;
}

@media only screen and (min-width: 992px) {
  form#user-login div#edit-info {
    flex: 2 1 20%;
    font-size: 11px;
    font-family: "Lato Regular";
    color: #000000;
    margin-left: 45px;
    display: block;
  }
}

form#user-login div#edit-enlace {
  display: flex;
  flex: 0 1 50%;
  justify-content: flex-end;
  padding-right: 16px;
  height: 36px;
  margin: 0px;
  margin-bottom: 22px;
}

form#user-login div#edit-enlace a {
  font-family: "Lato Regular";
  font-size: 1.5rem;
  color: #000000;
}

@media only screen and (min-width: 992px) {
  form#user-login div#edit-enlace {
    flex: 0 1 25%;
    justify-content: center;
    align-items: center;
    padding: 0px;
  }
}

form#user-login div#edit-actions {
  display: flex;
  flex: 0 1 50%;
  justify-content: flex-start;
  padding-left: 16px;
  margin-bottom: 22px;
}

form#user-login div#edit-actions button#edit-submit {
  background: #41B65B;
  border-color: #41B65B;
  border-radius: 0px;
  font-family: "Lato Regular";
  font-size: 1.5rem;
  color: #ffffff;
}

@media only screen and (min-width: 992px) {
  form#user-login div#edit-actions {
    flex: 0 1 21%;
    justify-content: flex-start;
    align-items: center;
    padding: 0px;
  }
}

@media only screen and (min-width: 992px) {
  form#user-login {
    width: 60%;
    height: 50vh;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

